.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 2px solid #999999;
  border-radius: 4px;
  background: #111;
  color: white;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  font-size: large;
  font-weight: bold;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #0069cc;
  border-color: #999999;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #b10000;
  border-color: #b10000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
