  {
      min-height: 250px;
  }

  img{
      height: 100%;
      width: 100%;
  }

  .imgBox h2{
    font-size: 1.2em;
    text-align: left;
  }

  .imgBox p{
    font-size: 0.8em;
    padding-top: 0;
    text-align: left;
  }

  .imgBox .txt::before{
    top: 30px;
    bottom: 90px;
  }
}





